.app {
  flex-basis: 450px;
  margin: 0 auto;
  padding: 20px;

  background: rgba(0, 0, 0, 0.49);
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.41);
  backdrop-filter: blur(4px);
  border-radius: 10px;
}

@media screen and (max-width: 450px) {
  .app {
    width: 100%;
    min-height: 100vh;
    border-radius: 0px;
  }
}