.search {
    display: flex;
    height: 35px;
    &__input {
        display: inline-block;
        width: 100%;
        height: 100%;
        padding: 0 10px;
        background: #FFFFFF;
        box-shadow: inset 2px 2px 4px 1px rgba(0, 0, 0, 0.25);
        border-radius: 10px 0px 0px 10px;
        border: none;
        &:focus {
            outline: none;
        }
    }
    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-basis: 50px;
        height: 100%;
        background: linear-gradient(180deg, #6D9DE0 0%, #0072ED 100%);
        border-radius: 0px 10px 10px 0px;
        border: none;
        cursor: pointer;
        &:hover {
            background: linear-gradient(180deg, #84b0ee 0%, #0f7bee 100%);
        }
    }
    &__icon {
        color: #fff;
    }
}